@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;700&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dialog, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video, input,
button, textarea, select {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
a{
	text-decoration: none;
}
dialog{
	box-shadow: none;
}

#root {
	font-family: "Montserrat", Arial, Helvetica, sans-serif;
	font-weight: 400;
	background-color: white;
	max-width: 1440px;
	margin: auto;
	padding: 0 5%;
}


.banner {
	border-radius: 10px;
	overflow: hidden;
	height: 7rem;
	margin-bottom: 1.25rem;
}

@media screen and (min-width: 768px) {
	.banner {
		border-radius: 25px;
		height: 14rem;
		margin-bottom: 2rem;
	}
}

.banner__background {
	width: 100%;
	height: 100%;
	object-fit: cover;
	filter: brightness(70%);
}

.hero {
	position: relative;
}

.hero__title {
	position: absolute;
	top: 50%;
	transform: translate(0, -50%);
	left: 1.25rem;
	font-style: normal;
	font-weight: 500;
	color: white;
	z-index: 2;
	font-size: 1.5rem;
	line-height: 1.5rem;
	width: 90%;
}

@media screen and (min-width: 768px) {
	.hero__title {
		font-size: 3rem;
		line-height: 4.25rem;
		top: 50%;
		transform: translate(-50%, -50%);
		left: 50%;
		text-align: center;
	}
}

.loader {
  min-height: 85vh;
  display: grid;
  place-items: center;
}

svg.tea {
  --secondary: #ff6060;
}
svg.tea #teabag {
  transform-origin: top center;
  transform: rotate(3deg);
  animation: swing 2s infinite;
}
svg.tea #steamL {
  stroke-dasharray: 13;
  stroke-dashoffset: 13;
  animation: steamLarge 2s infinite;
}
svg.tea #steamR {
  stroke-dasharray: 9;
  stroke-dashoffset: 9;
  animation: steamSmall 2s infinite;
}
@keyframes swing {
  50% {
    transform: rotate(-3deg);
  }
}
@keyframes steamLarge {
  0% {
    stroke-dashoffset: 13;
    opacity: 0.6;
  }
  100% {
    stroke-dashoffset: 39;
    opacity: 0;
  }
}
@keyframes steamSmall {
  10% {
    stroke-dashoffset: 9;
    opacity: 0.6;
  }
  80% {
    stroke-dashoffset: 27;
    opacity: 0;
  }
  100% {
    stroke-dashoffset: 27;
    opacity: 0;
  }
}

.rentalsList {
	display: grid;
	grid-gap: 1.25rem;
	/* justify-items: center; */
}

@media screen and (min-width: 768px) {
	.rentalsList {
		grid-template-columns: repeat(auto-fill, minmax(340px, 1fr));
		grid-gap: 3.125rem;
		padding: 3.125rem;
		border-radius: 25px;
		background-color: #f6f6f6;
	}
}

.thumb {
	height: 255px;
	overflow: hidden;
	border-radius: 10px;
	position: relative;
	transition: all 0.1s ease;
}

@media screen and (min-width: 768px) {
	.thumb {
		height: 340px;
	}
}

.thumb:hover {
	box-shadow: 5px 5px 20px 0px rgba(0, 0, 0, 0.15);
}

.thumb__cover {
	width: 100%;
	height: 100%;
	object-fit: cover;
	filter: brightness(70%);
	transition: all 0.1s ease;
}

.thumb__cover:hover {
	filter: brightness(100%);
}

.thumb__title {
	position: absolute;
	z-index: 2;
	color: white;
	font-weight: 500;
	font-size: 1.125rem;
	bottom: 1.25rem;
	left: 1.25rem;
	width: 90%;
}

.aboutContent__container {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.about .banner {
	height: 14rem;
}

.accordion {
	margin-bottom: 1.25rem;
	border-radius: 5px;
	max-width: 1023px;
	width: -moz-available;
	transition: all 0.15s ease;
}

@media screen and (min-width: 768px) {
	.accordion {
		margin-bottom: 2rem;
	}
}

.accordion__title {
	background-color: #ff6060;
	color: white;
	height: 2rem;
	font-size: 0.75rem;
	font-weight: 500;
	display: flex;
	justify-content: space-between;
	border-radius: 5px;
	align-items: center;
	padding: 0 1rem;
}

@media screen and (min-width: 768px) {
	.accordion__title {
		font-size: 1.5rem;
		height: 3rem;
	}
}

.accordion__arrow {
	width: 1rem;
	transition: transform 0.2s ease;
}

@media screen and (min-width: 768px) {
	.accordion__arrow {
		width: 1.5rem;
		padding: 0 1rem;
	}
}

.accordion__content {
	font-weight: 400;
	font-size: 0.75rem;
	color: #ff6060;
	line-height: 1.125rem;
	padding: 1rem;
}

@media screen and (min-width: 768px) {
	.accordion__content {
		font-size: 1.5rem;
		line-height: 2.125rem;
	}
}

.accordion[open] .accordion__arrow {
	transform: rotate(180deg);
}

.accordion[open] {
	background: #f7f7f7;
}

.accordion[open] .accordion__content {
	animation: animateDown 0.2s linear ;
}

@keyframes animateDown {
  0% {
    opacity: 0;
    transform: translatey(-15px);
  }
  100% {
    opacity: 1;
    transform: translatey(0);
  }
}

.rentalContent__container {
	display: flex;
	flex-direction: column;
}

@media screen and (min-width: 768px) {
	.accordion__title {
		font-size: 1.125rem;
	}
	.accordion__content {
		font-size: 1.125rem;
		line-height: 1.625rem;
	}
	.rentalContent__container {
		flex-direction: row;
		grid-gap: 5%;
		gap: 5%;
	}
}

.info__container {
	display: flex;
	flex-direction: column;
	grid-gap: 1rem;
	gap: 1rem;
	margin: 2rem 0;
}

@media screen and (min-width: 768px) {
	.info__container {
		flex-direction: row;
		justify-content: space-between;
	}
}

.host__container {
	display: flex;
	flex-direction: row-reverse;
	justify-content: space-between;
	align-items: center;
}

@media screen and (min-width: 768px) {
	.host__container {
		flex-direction: column;
		align-items: flex-end;
	}
}

.rental__title {
	color: #ff6060;
	font-weight: 500;
	font-size: 1.125rem;
	line-height: 1.75rem;
}

@media screen and (min-width: 768px) {
	.rental__title {
		font-size: 2.25rem;
		line-height: 3.125rem;
	}
}

.rental__location {
	color: #ff6060;
	font-weight: 500;
	font-size: 0.875rem;
	line-height: 1.25rem;
}

@media screen and (min-width: 768px) {
	.rental__location {
		font-size: 1.125rem;
		line-height: 1.75rem;
	}
}

.tag__box {
	background-color: #ff6060;
	color: white;
	border-radius: 5px;
	min-width: 5.25rem;
	display: flex;
	justify-content: center;
}

@media screen and (min-width: 768px) {
	.tag__box {
		border-radius: 10px;
		min-width: 7.25rem;
	}
}

.tag__content {
	font-weight: 500;
	font-size: 0.6rem;
	line-height: 0.75rem;
	padding: 0.25rem 1.25rem;
	white-space: nowrap;
}

@media screen and (min-width: 768px) {
	.tag__content {
		font-size: 0.875rem;
		line-height: 1.25rem;
	}
}

.tag__container {
	display: flex;
	grid-gap: 0.6rem;
	gap: 0.6rem;
	margin-top: 0.5rem;
	flex-wrap: wrap;
}

@media screen and (min-width: 768px) {
	.tag__container {
		margin-top: 1.25rem;
	}
}

.host {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: flex-end;
	grid-gap: 0.5rem;
	gap: 0.5rem;
}

.host__picture {
	border-radius: 100%;
	width: 2rem;
}

@media screen and (min-width: 768px) {
	.host__picture {
		width: 4rem;
	}
}

.host__name {
	color: #ff6060;
	font-weight: 500;
	font-size: 0.75rem;
	line-height: 1.125rem;
}

@media screen and (min-width: 768px) {
	.host__name {
		font-size: 1.125rem;
		line-height: 1.5rem;
	}
}

.rating {
	display: flex;
	flex-wrap: nowrap;
	grid-gap: 0.25rem;
	gap: 0.25rem;
}

@media screen and (min-width: 768px) {
	.rating {
		grid-gap: 0.5rem;
		gap: 0.5rem;
		margin-top: 1.5rem;
	}
}

.rating__star {
	width: 1rem;
}

@media screen and (min-width: 768px) {
	.rating__star {
		width: 2rem;
	}
}

.carousel {
	border-radius: 10px;
	height: 14rem;
	overflow: hidden;
	position: relative;
}

@media screen and (min-width: 768px) {
	.carousel {
		border-radius: 25px;
		height: 26rem;
	}
}

.carousel___image {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.carousel__page {
	position: absolute;
	z-index: 2;
	color: white;
	font-weight: 500;
	font-size: 1.125rem;
	bottom: 1.25rem;
	left: 50%;
}

.carousel__arrow-left {
	transform: rotate(90deg);
	position: absolute;
	z-index: 2;
	left: 1.25rem;
	bottom: 50%;
	width: 1.25rem;
}

@media screen and (min-width: 768px) {
	.carousel__arrow-left {
		width: 5rem;
	}
}

.carousel__arrow-right {
	transform: rotate(270deg);
	position: absolute;
	z-index: 2;
	right: 1.25rem;
	bottom: 50%;
	width: 1.25rem;
}

@media screen and (min-width: 768px) {
	.carousel__arrow-right {
		width: 5rem;
	}
}

.error {
	display: flex;
	flex-direction: column;
	align-items: center;
	color: #ff6060;
}

.error__title {
	font-weight: 700;
	font-size: 6rem;
	line-height: 8.5rem;
	margin-top: 10vh;
}

@media screen and (min-width: 768px) {
	.error__title {
		font-size: 18rem;
		line-height: 25rem;
	}
}

.error__info {
	font-weight: 500;
	font-size: 1.125rem;
	line-height: 1.5rem;
	text-align: center;
}

@media screen and (min-width: 768px) {
	.error__info {
		font-size: 2.25rem;
		line-height: 3rem;
	}
}

.error__link {
	color: inherit;
	font-weight: 500;
	font-size: 0.75rem;
	margin-top: 20vh;
}

@media screen and (min-width: 768px) {
	.error__link {
		font-size: 1.125rem;
	}
}

.error__link:hover {
	text-decoration: underline;
}

.header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	grid-gap: 1rem;
	gap: 1rem;
	margin: 2rem 0;
	flex-wrap: wrap;
}

.header__logo {
	max-width: 210px;
	height: 3rem;
}

@media screen and (min-width: 768px) {
	.header__logo {
		height: 4.25rem;
	}
}

.header__nav {
	display: flex;
	flex-wrap: nowrap;
	grid-gap: 0.75rem;
	gap: 0.75rem;
}

@media screen and (min-width: 768px) {
	.header__nav {
		grid-gap: 3.5rem;
		gap: 3.5rem;
	}
}

.nav__home,
.nav__about {
	font-weight: 500;
	font-size: 0.75rem;
	color: #ff6060;
}

@media screen and (min-width: 768px) {
	.nav__home,
	.nav__about {
		font-size: 1.5rem;
	}
}

.nav__home:hover,
.nav__about:hover,
.nav__home-active,
.nav__about-active {
	text-decoration: underline;
}

.footer {
	background-color: black;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	margin-top: 2rem;
	grid-gap: 2.5rem;
	gap: 2.5rem;
	padding: 3.75rem;
}

.footer__copyright {
	color: white;
	text-align: center;
	font-weight: 500;
	font-size: 0.75rem;
	line-height: 1rem;
}

@media screen and (min-width: 768px) {
	.footer__copyright {
		font-size: 1.5rem;
		line-height: 2rem;
	}
}

